import React from 'react';
import PropTypes from 'prop-types';

import Icon28Notifications from '@vkontakte/icons/dist/28/notifications';

import '../css/BlockItem.css';

const BlockItem = ({img, title, cat, desc}) => (
    <div className='BlockItem'>
		<div className="BlockFavorite"><Icon28Notifications /></div>
		<div className='BlockImage'><img className="BlockItemImage" src={img} alt="BlockItemImage"/></div>
		<div className='BlockInfo'>
			<div className='BlockTitle'>{title}</div>
			<div className='BlockCategory'>{cat}</div>
			<div className='BlockDescp'>{desc.length > 60 ? desc.substring(0, 60 - 3) + '...' : desc}</div>
		</div>
	</div>
);

BlockItem.propTypes = {
    img: PropTypes.string,
    title: PropTypes.string,
    cat: PropTypes.string,
    desc: PropTypes.string
};

export default BlockItem;